<template>
  <div class="left-menus">
   <nav  :class="{toggleLDiv: searchTypeToggle}" style="font-size: 0.94em">                                                                                
         <div class="center-menu" v-show="!searchTypeToggle" >
              <inputs ref="inputs"  :option="FormOpt[0].options" style="margin:1em 0.65em;" 
                         @getInput="getFroms" v-if="!noSearch"></inputs>
              <!--<inputs ref="inputs"  :option="FormOpt[1].options" style="margin:1em 0.65em;"  v-if="FormOpt[1].show"
                         @getInput="getFroms"></inputs>-->
              <div class="result-container" :class="{noSearchCon: noSearch}">
                 <div class="treeContainer">
                   <div style="width: calc(100% - 1em); height:calc(100% - 0.5em);padding: 0em 0.5em;font-size:0.875em">
                      <VuePerfectScrollbar style="width:100%;height:100%;position:relative">
                          <Tree v-if="dataReady" ref="myTree"   :treeOpt="treeOpt" :isInit="true" :singleSelect="singleSelect" @getSingle="getSingleNode"  :hasInitSelect="hasInitSelect" @getInitSelect="getSingleNode"></Tree>
                      </VuePerfectScrollbar>
                   </div>
                 
                 </div>
          </div>
      </div>
   </nav>
   <dialogs type="alert" ref="alerts"></dialogs>
 </div>
</template>
<script>
  import Tree from '../../childComponent/myTree.vue'
  import VuePerfectScrollbar from 'vue-perfect-scrollbar'
  export default {
    data() {
      return {
         searchTypeToggle: false, //收缩左侧栏标识
         dataReady: true,
         searchTree: {
            projectName: ''
         }, 
         selectedNode: [], //从搜索结果里面选中的设备
         showTree: false,
         treeOpt: {}
      }
    },
    props: [ 'singleSelect', 'hasInitSelect', 'noSearch', 'noLastChild'],
    computed: {
      FormOpt() {
         return [  //左侧模糊匹配输入框对象
                
                  {
                        type: 'input',
                        show: true,
                        options: {
                            width: 'calc(100% - 1.3em)',
                            type: 'typeSearch',
                            key: 'searchTree.projectName',
                            value: '',
                            placeholder: '请输入关键字',
                            length: 20,
                            hideSelect: true,
                            noBorderSet: true
                         }
                  }
              ]
      }
    },
    components: {
      Tree,
      VuePerfectScrollbar
    },
    methods: {
       getSingleNode: function (val) {
       	 // console.log(val)
         this.$emit('getSingleNode', val)
       },
       getTreeOpt: function () { //左侧树
          let url;
          switch(this.searchTree.projectName) {
            case '': {
              url = 'tree/projectTree';
              break;
            }
            default: {
              url = 'tree/projectfuzzyQueryTree';
            } 
          }
          let queryType = 'object';
          let param = this.getSearchData(this.searchTree);
          this.treeOpt  = {
             init: true,
             url: url,
             method: 'post',
             param: param,
             queryType: queryType,
             searchVal: [this.searchTree.projectName],
             childUrl: 'tree/projectTreeMainSubTree',
             noLastChild: this.noLastChild
          }
       },
      
       getFroms: function (val) {
         if(val.key === 'searchTree.projectName') {
          if(val.val !== this.searchTree.projectName) {
            this.getOption(val, true);
            this.dataReady = false;
            this.getTreeOpt();
            let that =this;
            that.$nextTick(function () {
             that.dataReady = true;
           });
          }  
        } 
      },
      toggleSearchType: function () {
         this.searchTypeToggle = !this.searchTypeToggle;
      },
     
      noTreeData: function (val) {
         this.$emit('noData', '')
      },
        //获取树形节点
       getNodes: function (val) {
          this.selectedNode = val;
       }
    },
    created: function () {
      this.getTreeOpt();
    }
  }
</script>
<style lang="scss" >
  @import "../../../assets/main.scss";
  $searchTypeWidth: 2.7em;
  $leftMenu: 19.5em;
  .left-menus{
     box-shadow: 2px 2px 5px rgba(0,0,0,0.3);
     margin-top: 0em;
     margin-left: 0em;
     width: $leftMenu;
     height: calc(100% - 0em);
     background: rgba(#3588fa, 0.3);
     border-radius: 0.4em;
     color: white;
     float:left;
     position: relative;
     nav{
      height:100%;
      width:100%;
     }

     .center-menu{
       width: calc(100% );
       display: inline-block;
       height: 100%;
       .input-container {
         font-size: 0.93em;
         border:1px solid #ADACBA;
         border-radius: 0.3em;
         .selectBtn .hasIcon{
          margin-right: 3px;
         }
         input,button{
          background: transparent;
          border:0!important
         }
       }
       .mySelect .selectBtn{
         border:1px solid #ADACBA;
         background: transparent
       }
       .typeSelect .selectBtn{
           border: 0
       }
       .noBackS .selectBtn{
           border: 0;
           background: transparent;
           font-size: 1em;
           .hasIcon{
            margin-right:1em
           }
       }
       .result-container{
          height: calc(100% - 2em - 2.75em*0.94);
          float:left;
          padding: 0 0.2em;
          width: calc(100% - 0.4em);
          .treeContainer{
             height:100%;
          }
       }
       .noSearchCon{
       	  padding: 0.5em 0.2em;
       	  height: calc(100% - 1em);
       }
     
     }
     .hasSearchVal{
        color: #C43829;
     }
  }
</style>


