<template>
    <div ref="trys" style="height: calc(100%)">
        <div v-if="showInfo" class="info-container">
            <info :row="row" @closeDiv="closeDiv" :controls="controls" ref="infoDetail"></info>
        </div>
        <div v-show="!showInfo"  class="all-container noback-Container">
          <searchTree  @getSingleNode="getSelectNode" :noCustomer="true" :singleSelect="true"  ></searchTree>
          <div class="TreeRight TreeRights" >
             <div class="treeRightTop">
             	<div class="contents contentsBlue">
             		<span class="innerSmall">设备总数量</span>
             		<span class="innerBig">{{deviceAccount.total}}</span>
             	</div>
             	<div class="contents contentsOrange">
             		<span class="innerSmall">运行中数量</span>
             		<span class="innerBig">{{deviceAccount.onTotal}}</span>
             	</div>
             	<div class="contents contentsRed">
             		<span class="innerSmall">故障中数量</span>
             		<span class="innerBig">{{deviceAccount.errorTotal}}</span>
             	</div>
             	<div class="contents contentsYellow">
             		<span class="innerSmall">空闲中数量</span>
             		<span class="innerBig">{{deviceAccount.freeTotal}}</span>
             	</div>
             	<div class="contents contentsGrey">
             		<span class="innerSmall">离线中数量</span>
             		<span class="innerBig">{{deviceAccount.offlineTotal}}</span>
             	</div>
             </div>
             <div class="treeRightBot">
             	<div class="list-header" style="border:0;float:left">
                    <i class="hasIcon hasImg">
                        <img src="dist/img/widsomApp/settings.png" />
                    </i>设备详细信息  {{currentNode === '' ? '' : `( ${currentNode} )`}} 
                </div>
                <div class="operate-div" ref="operateDiv" style="padding-top:0">
                     <template v-for="(item, $index) in formList">
                       <selects ref="selects" v-if="item.type == 'select'" :option="item.options" @getSelect="getFrom"></selects>
                       <inputs ref="inputs" v-if="item.type == 'input'" :option="item.options"  @getInput="getFrom"></inputs>
                    </template>
                </div>
             	<div ref="myOwnTable" class="table-container" :style="{height: 'calc(100% - 3em)'}">
                 <tables ref="myTable" :tableOpt="TableOpt" class="tablePadding"  v-if="showTable" classes="hasBorderTable"
                      @rowDbClick="clickTableRow"></tables>
                 </div>
             </div>
          </div>
        </div>
      <dialogs type="confirm" ref="conformDialog"></dialogs>
      <dialogs type="alert" ref="alerts"></dialogs>
     
    </div>
</template>
<script>
    import info from './Info.vue'
    import searchTree from '../child/leftSearchMenu.vue'
    export default {
        data() {
            return {
                showTable: true,
                showInfo: false,
                searchParam: {
                     deviceNo: '',
                     commNo: '',
                     payCategory: 0,
                     runState: '',
                     online: '',
                     nodeType:'',
                     nodeId: ''
                },
                controls: [],
                TableOpt: {
                    column: [],
                    url: 'charger/monitorDeviceList',
                    queryParam: {payCategory: 0},
                    method: 'post',
                    pageSize: 20,
                    pageNumber: 1,
                    pageList: [20,40,60],
                    exportURL: 'charger/monitorDeviceListDown',
                    isExport: false
                },
                deviceAccount: {
          	        total: 0,
                	onTotal: 0,
                	freeTotal: 0,
                	errorTotal: 0,
                	offTotal: 0,
                	outLine: 0,
                    offlineTotal: 0
               },
               currentNode: ''
            }
        },
        components: {
            info,
            searchTree
        },
        computed: {
        	 formList() {
        	   let arr1 = [
        	     {text: '全部状态', value: '*_*'},
        	     {text: '运行', value: '1_1'},
        	     {text: '故障', value: '-1_1'},
        	     {text: '空闲', value: '0_1'},
        	     {text: '停运', value: '2_1'},
        	     {text: '离线', value: '*_0'},
        	   ];
        	   let arr2 = [
        	     {text: '全部', value: 0},
        	     {text: '临时充值', value: 1},
        	     {text: '包月充值', value: 2},
        	     {text: '一次充满', value: 3},
        	     {text: '余额活动充值', value: 4},
        	     {text: 'IC卡充值', value: 5}
        	   ]
               return [
                 {
                   type: 'select',
                   options: {
                     width: '10em',
                     cs: 'client',
                     data: arr1,
                     key: 'searchParam.runState'
                   }
                 },
                 {
                   type: 'select',
                   options: {
                     width: '10em',
                     cs: 'client',
                     data: arr2,
                     key: 'searchParam.payCategory'
                   }
                 },
                 {
                   type: 'input',
                   options: {
                     type: 'typeSearch',
                     key: 'searchParam',
                     selectOpt: {
                       width: '8em',
                       cs: 'client',
                       data: [
                        {text: '设备编号', value: 'deviceNo'},
                        {text: '通信编号', value: 'commNo'},
                       ]
                     }
                   }
                 }
               ]
      	    },
             columns() {
              let arr = [
                        {
                            title: '运行状态',
                            field: 'isOnline',
                            textAlign: 'center',
                            sortable: false,
                            width: '100',
                            type: 'object',
                            formatter: function (val, index, row, obj) {
                            	let parent = obj.$parent;
                                let template;
                                let states = parent.getState(row.runState, row.online);
                                if(row.runState == 1) {
                                	template = `<div class="deviceDivs" >
         			                            <div class="content" >
         			  	                          <div  class="${row.carCategory ==2 ? 'hasPercent hasPercentCar' : 'hasPercent'}" >
                                                   <div class="${row.carCategory ==2 ? 'imgLeft imgLeftCar' : 'imgLeft'}" style="width: ${row.percent}%"></div>
                                                   <div class="${row.carCategory ==2 ? 'imgRight imgRightCar' : 'imgRight'}" ></div>
                                                   <div class="imgTop"></div>
         			  	                          </div>
         			                       	      <span class="centerSpan" style="margin-top:0.5em">${states.text} ${row.percent}%</span>
         			                           </div></div>`
                                } else {
                                	  template = `<div class="deviceDivs" >
         			                        <div class="content" >
         			  	                      <img src="dist/img/widsomApp/${states.img}.png"  />
         			                       	  <span class="centerSpan">${states.text}  </span>
         			                        </div></div>`
                                }
                               
                                return {
                                	template: template
                                }
                            }
                        },
                        {title: '设备号', field: 'deviceNo', textAlign: 'center', width: '100', sortable: false},
                        {
                          title: '计费方式',
                          field: 'payCategory',
                          textAlign: 'center',
                          width: '100',
                          sortable: false,
                          type: 'string',
                          formatter(val, index, item, obj) {
                          	return `<div>${obj.$parent.getEnumItem('payCategory', val).text || '--'}</div>`
                          }
                        },
                        {title: '充电时长(分钟)', field: 'useTime', textAlign: 'center', width: '80', sortable: false},
                        {title: '电流（A）', field: 'current', textAlign: 'center', width: '80', sortable: false},
                        {title: '电压（V）', field: 'voltage', textAlign: 'center', width: '80', sortable: false},
                        {title: '功率（W）', field: 'power', textAlign: 'center', width: '80', sortable: false},
                        {title: '电量（KWH）', field: 'usePower', textAlign: 'center', width: '80', sortable: false},
                        {title: '剩余时长(分钟)', field: 'remainTime', textAlign: 'center', width: '80', sortable: false, type: 'string', formatter: function (val) 
                           {
                           	  return `<span style="color:#CC7638">${val}</span>`
                           }
                        },
                        {title: '使用人员', field: 'customerName', textAlign: 'center', width: '100', sortable: false},
//                      {title: '操作', field: 'remark', align: 'center', textAlign: 'middle', width: '120', type: 'object', 
//                       formatter: function (val,index, row, obj) {
//                       	let str = `<div><button class="list-operate"  @click="operateDevice(2)">关闭</button> | 
//                       	<button class="list-operate"  @click="operateDevice(1)">开启</button></div>`;
//                       	return {
//                       		template: str,
//                       		methods: {
//                       			operateDevice: function (flag) {
//                                     obj.$parent.operateDevice(flag, row)
//                       			}
//                       		}
//                       	}
//                       }
//                      }
                    ]
          
             return arr;  
            }
        },
        methods: {
        	submitOpe: function (confirmMsg, operateUrl, param) {
               let that = this;
               let alerts = this.$refs.alerts;
               this.$refs.conformDialog.show({
                  msg: confirmMsg,
                  action: {
                     confirm: function () {
                         that.getRequest({
                             url: operateUrl,
                             param: param, 
                             method: 'post',
                             success: function (data) {
                                   alerts.show({msg: data.message});                             
                                   that.getParams(1);
                             },
                             error: function (data) {
                               alerts.show({msg: data.message})
                             }
                          })
                     }
                  }
               })
            },
        	operateDevice: function (flag, row) {
        		let param = {};
                param.commNo = row.commNo;
                param.port = row.port;
                param.onOrOff = flag === 2 ? 0 : 1;
                this.submitOpe(`确定要${flag === 2 ? '关闭' : '启用'}该设备吗？`, 'charger/offOn', param);
        	},
        	stopAccs: function () {
               let that = this;
               that.getWebsocket({
                  keyMsg: 'onOffDevice',
                  sign: true,
                  funMsg: function (data) {
                     let datas = data.data;
                     that.$refs.myTable.updateRow(null, datas, ['deviceNo', 'port'])
                  }
                })
            },
        	deviceUpdate: function () {
              let that = this;
              that.getWebsocket({
                 keyMsg: 'monitorDeviceList',
                 sign: true,
                 funMsg: function (data) {
                   let datas = data.data;
                   that.$refs.myTable.updateRow(null, datas, ['deviceNo', 'port'])
                 }
              });
            },
            
        	getSearchParam: function (flag) {
              let param = this.searchParam;
              param = this.getSearchData(param);
              if(flag) {
              	param = {
              		nodeId: this.searchParam.nodeId,
              		nodeType: this.searchParam.nodeType
              	}
              }
              return param;
            },
        	getStateAccount: function () {
              let that = this;
              that.getRequest({
                  url: 'charger/monitorDevice',
                  method: 'post',
                  param: that.getSearchParam(true),
                  success: function (data) {
                      let Data = data.data;
                      for(let key in that.deviceAccount) {
                         that.$set(that.deviceAccount, key , Data[key] || 0)
                      }
                  }
              })
            },
            getState: function (state, online) {
              if(online != 1) {
                 return {
                     text: '离线',
                     img: 'outline_icon'
                 };
              } 
              let s = typeof state !== 'undefined' || state == 'undefined' ? state.toString() : '';
              switch(s) {
          	    case '1': 
          	      return {
          	  	     text: '充电中',
          	  	     img: 'ddcs'
          	      };
             	case '0': 
          	      return {
          	  	     text: '空闲',
          	  	     img: 'free_icon'
          	      };
          	    case '-1': 
          	      return {
          	  	     text: '异常',
          	  	     img: 'alarm_icon'
          	      };
          	    case '2': 
          	      return  {
          	  	     text: '停运',
          	  	     img: 'forbidon'
          	      };
                default: {
                  return  {
                     text: '',
                     img: ''
                  };
                }
              }
           },	
            getSelectNode: function(val) {
              this.searchParam.nodeId = val.nodeId;
              this.searchParam.nodeType = val.nodeType || 1;
              if(!this.showTable) {
                this.TableOpt.queryParam.nodeId = val.nodeId;
                this.TableOpt.queryParam.nodeType = val.nodeType
                this.showTable = true;
              } else {
                this.getParams(1);
              }
              this.getStateAccount();
              this.currentNode = val.nodeName
            },
          
            getFrom: function (val) {
              if (val.combine) {
                if (this.searchParam[val.key.split('.')[1]] !== val.val ) {
                  this.searchParam = this.cleanSearchData(this.searchParam, ['payCategory', 'runState', 'online','nodeType','nodeId']);
                  this.getOption(val, true);
                  this.getParams(1);
                }
              } else {
              	if(val.key === 'searchParam.runState') {
              		let arr = val.val.split('_');
              		let state =  arr[0] === '*' ? '' : arr[0];
              		let online = arr[1] === '*' ? '' : arr[1];
              		if(!(this.searchParam.runState === state && this.searchParam.online === online)){
              			this.searchParam.runState = state;
              		    this.searchParam.online = online;
              		     this.getParams(1);
              		}
              		
              	} else {
              		if(this.searchParam[val.key.split('.')[1]] !== val.val) {
              			 this.getOption(val, true);
              			 this.getParams(1);
              		}
              	}
              }
            },
            removeSearchData: function (index) {
                let key = this.deepSearchData[index].key;
                if (key === 'isOnline') {
                    this.$refs['selects'][1].constant = {text: '全部状态', value: ''};
                    this.searchParam.isOnline = '';
                }
               else if (key === 'deviceType') {
                    this.$refs['selects'][0].constant =    {text: '全部类型', value: ''};
                    this.searchParam.deviceType = '';
                }
                else {
                    this.searchParam[key] = this.$refs['inputs'][0].search = '';
                   this.getParams(1);
                }
            },
            closeDiv: function () {
                this.showInfo = false;
                this.getParams();
                this.deviceUpdate();
                this.stopAccs()
            },
            
            getParams: function (pageNumber) {
              this.deepSearchData = [];
              let arr = this.getSearchData(this.searchParam);
              this.getHeight();
              this.searchTable(arr, pageNumber);
            },
            clickTableRow: function (val) {
                this.row = val.val;
                this.showInfo = true
            },
            resizeCon: function () {
               this.getHeight();
               if (this.$refs.infoDetail) {
                  let info = this.$refs.infoDetail;
                  info.lineR !== ''?  info.lineR.resize() : '';
                  info.lineA !== ''?  info.lineA.resize() : '';
                  info.lineV !== ''?  info.lineV.resize() : '';
                }
            },
            windowChange: function () {
               let that = this;
              that.resizeCon()
              // window.onresize = function () {
              //   that.getHeight('calc(100% - 4.5em)');
              // }
            },
            init: function () {
            	this.TableOpt.isExport = this.controls[6];
                this.deviceUpdate();
                this.stopAccs()
            }    
        },
        created: function () {
            this.TableOpt.column = this.columns;
            this.getStateAccount();
        },
        mounted: function () {
            let that = this;
            that.isIn();
            that.getAction();
        },
        beforeRouteEnter(to, from, next) {
           next(vm => { 
               if(vm.showInfo) {
                  let obj = vm.$refs.infoDetail;
                  obj.init();
               } else {
               	  vm.deviceUpdate();
                  vm.stopAccs()
               }
          });
        },
        watch: {
          
        }
    }
</script>
<style lang="scss" >
   @import "../../../assets/main.scss";
   $TopH: 6em;
  .right-container .center-container .noback-Container{
    background: transparent;
    padding:0;
    width:100%;
  }
    .TreeRights{
    	padding: 0!important;
        width: calc(100% - 19.5em - 0.6em) !important;
        background: transparent !important;
        margin-left:0.6em !important;
    }
   .treeRightTop, .treeRightBot{
   	 width: 100%;
   	 background: rgba(53, 136, 250, 0.3);
   	 border-radius:0.4em;
   	 
   }
  .treeRightTop{
  	height: $TopH;
  	display: flex;
   	 div.contents{
   	 	flex: 1;
   	 	background-repeat: no-repeat;
   	 	background-size: contain;
   	 	background-position:center ;
   	 	display: flex;
        justify-content: center;
        align-items: center;
         margin: 0 2.5%;
         padding-right: 4%;
         padding-left: 1.1%;
   	 	span.innerSmall{
   	 		font-size:0.84em;
   	 		flex: 2;
   	 		text-align: left;
   	 	}
   	 	span.innerBig{
   	 		font-size:0.94em;
   	 		flex: 1;
   	 		text-align: right;
   	 	}
   	 }
   	 .contentsBlue{
   	 	background-image: url('../../../../dist/img/widsomApp/blue.png')
   	 }
   	  .contentsGrey{
   	 	background-image: url('../../../../dist/img/widsomApp/grey.png')
   	 }
   	  .contentsOrange{
   	 	background-image: url('../../../../dist/img/widsomApp/orange.png')
   	 }
   	  .contentsRed{
   	 	background-image: url('../../../../dist/img/widsomApp/red.png')
   	 }
   	  .contentsYellow{
   	 	background-image: url('../../../../dist/img/widsomApp/yellow.png')
   	 }
  }
  .treeRightBot{
  	margin-top:0.6em;
  	padding: 0.8em;
  	width: calc(100% - 1.6em);
  	height: calc(100% - #{$TopH} - 0.6em - 1.6em);
  	 .deviceDivs{
           width:100%;
           cursor:pointer;
           height:auto;
           float:left;
           .content{
           	  height: 100%;
           	  width:100%;
           
              img{
              	//margin-top:1em;
              	width:2.25em;
              	height:2.25em
              }
              img.centerImg{
              	//margin-top:1.8em
              }
              span{
              	width:100%;
              	display:block;
              	font-size:0.8em;
              }
              span.centerSpan{
               //	margin:0.4em 0 1em 0;
              }
              .hasPercent{
               // margin-top:1em;
                position:relative;
                left:calc((100% - 3.2em) / 2);
                width: 3.375em;
                height: 2.57em;
              }
              .hasPercentCar{
              	 width: 3.4em;
              }
              div.imgLeft{
                 width:0;
                 height:100%;
                 top:0;
                 left:0;
                 position:absolute;
                 background: url('../../../../dist/img/widsomApp/electrombileS.png') no-repeat;
                 background-size:cover;
               //  background: rgba(99,230,142, 1);
                 z-index: 2;
               //  animation:mymoveL 5s infinite;
               //  -webkit-animation:mymoveL 5s infinite; /*Safari and Chrome*/
              }

              @keyframes mymoveL
               {
                 from {width:0;}  to {width:100%}
               }
              @-webkit-keyframes mymoveL 
               {
                 from {width:0;}  to {width:100%}
               }
              div.imgRight{
                 width: 100%;
                 height:100%;
                 top:0;
                 right:0;
               //  background: white;
                 position:absolute;
                 z-index: 1;
                 background: url('../../../../dist/img/widsomApp/electrombile.png') no-repeat;
                 background-size:cover;
                 // animation:mymoveR 5s infinite;
                 //-webkit-animation:mymoveR 5s infinite; /*Safari and Chrome*/
              }
              div.imgLeftCar{
              	 background: url('../../../../dist/img/widsomApp/electricCars.png') no-repeat;
                 background-size:cover;
              }
               div.imgRightCar{
              	 background: url('../../../../dist/img/widsomApp/electricCar.png') no-repeat;
                 background-size:cover;
              }
              @keyframes mymoveR
               {
                 from {width:100%;}  to {width:0}
               }
              @-webkit-keyframes mymoveR 
               {
                 from {width:100%;}  to {width:0%}
               }
              div.imgTop{
                 position:absolute;
                 z-index: 2;
                 width:100%;
                 height:100%;
              //   background: url('../../../../dist/img/widsomApp/electrombile.png') no-repeat;
              //   background-size:cover;
              }
              div.imgTopDDC{
                  background-image: url('../../../../dist/img/widsomApp/ddc.png') ;
              }    
           }
           .noUse{
               background: rgba(48,72,109, 1)
            }
            .inDanger{
              border-color: rgba(238,86,72, 1)
            }
           p{
           	  font-size:0.84em;
              margin-bottom: 0;
              width: 100%;
              text-overflow: ellipsis;
              white-space: nowrap;
              overflow: hidden;
           }
   	 	}
  }
</style>



